

img {
    max-width: 80%;
    margin-bottom: 3rem;
    align-content: center;
    max-height: 60%;
}

p {
    // white-space: pre-line;
    word-break: normal ;
    max-width: 80%;
    flex-shrink: 1;
    margin-bottom: 5rem;
    margin-top: 3rem;
    justify-content: center;
    text-align: center;
}

.hide {
    // animation: fadein 1s linear forwards;
    opacity: 0;
    display: none;
    // animation-delay: 2s;
}

.button-order {
    cursor: pointer;
    outline: none;
    margin-bottom: 5rem;
    padding: 0.75rem 1.25rem;
    // padding-left: 1.5rem;
    
    font-size: 1.25rem;
    font-family: inherit;
    border-radius: 30%;
    border: 1px solid transparent;
    transition: 0.3s ease all;
    // margin-bottom: $spacing-md;
}

.headerShow {
    animation: headerfadein ease 1s;
}

.textShow {
    animation: textfadein ease 3s;
}

.secondHeaderShow {
    animation: secondHeaderFadeIn ease 5s;
}

.secondTextShow {
    animation: secondTextFadeIn ease 6s;
}

.secondImageFadeIn {
    animation: secondImageFadeIn ease 2s;
}


.show {
    animation: fadein ease 4s;
}

@keyframes headerfadein {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes textfadein {
    0% {
        opacity: 0;
        transform: translateY(-10px);
    }
    30% {
            opacity: 0;
            transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadein {
    0% {
        opacity: 0;
        transform: translateY(-10px);
    }
    45% {
        opacity: 0;
        transform: translateY(-10px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes secondHeaderFadeIn {
    0% {
        opacity: 0;
        transform: translateY(-10px);
    }
    45% {
        opacity: 0;
        transform: translateY(-10px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes secondTextFadeIn {
    0% {
        opacity: 0;
        transform: translateY(-10px);
    }
    45% {
        opacity: 0;
        transform: translateY(-10px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes secondImageFadeIn {
    0% {
        opacity: 0;
        transform: translateY(-10px);
    }
    40% {
        opacity: 0;
        transform: translateY(-10px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}
